/**
 * DuckDBSQL
 * Apache Arrow
 * @param {import('@duckdb/duckdb-wasm').AsyncDuckDB} db - DuckDB
 * @param {string} sqlQuery - SQL
 * @returns {Promise<import('apache-arrow').Table|null>} Apache ArrowTablenull
 */
export async function executeQuery(db, sqlQuery) {
  // DBnull
  if (!db || !sqlQuery) return null;

  // DB
  const conn = await db.connect();

  try {
    // ---  ---
    // httpfsspatial
    await conn.query("INSTALL httpfs;");
    await conn.query("LOAD httpfs;");
    await conn.query("INSTALL spatial;");
    await conn.query("LOAD spatial;");

    // ---  ---
    // SQL
    const result = await conn.query(sqlQuery);
    return result;

  } finally {
    // ---  ---
    // 
    await conn.close();
  }
}